<template>
  <div>
    <label for="dateline" class="col-form-label">
      <font-awesome-icon fixed-width class="ml-auto text-dark" size="lg" icon="folder-open" />
      {{todoGroup.group}}
    </label>

    <template v-for="item in todoGroup.todos">
      <TodoSingleRow :todo='item' :key="item.id" v-if="!item.isCheck"/>
    </template>

    <div v-if="hasIsCheckTodoGroup" class="text-center">
      <button type="button"
        class="btn btn-outline-primary py-1 my-2"
        @click="isShowCheckedTodo = !isShowCheckedTodo">顯示已完成項目</button>
    </div>

    <div v-if="isShowCheckedTodo">
      <template v-for="item in todoGroup.todos">
        <TodoSingleRow :todo='item' :key="item.id" v-if="item.isCheck"/>
      </template>
    </div>

  </div>
</template>

<script>
import TodoGroupRow from '@/components/template/todo-group-row.vue'
import TodoSingleRow from '@/components/template/todo-single-row.vue'

export default {
  components: {
    TodoGroupRow,
    TodoSingleRow
  },
  data () {
    return {
      isShowCheckedTodo: false,
      todoGroup: {
        group: 'today',
        id: 10,
        todos: [
          {
            id: 101,
            name: 'buy brackfast',
            dateLine: '2018/06/19',
            group: 'today',
            location: '中山區',
            note: 'note something',
            isCheck: true,
            isImportant: false
          },
          {
            id: 103,
            name: 'work',
            dateLine: '2018/06/19',
            group: 'today',
            location: '中山區',
            note: 'note something',
            isCheck: false,
            isImportant: true
          }, {
            id: 20,
            name: 'buy lunch',
            dateLine: '2018/06/19',
            group: 'today',
            location: '中山區',
            note: 'note something',
            isCheck: false,
            isImportant: false
          }]
      }
    }
  },
  computed: {
    hasIsCheckTodoGroup () {
      let todoList = this.todoGroup.todos
      return todoList.filter(item => item.isCheck === true).length > 0
    },
    isCheckTodoItems () {
      let todoList = this.todoGroup.todos
      return todoList.filter(item => item.isCheck === true)
    }
  },
  methods: {
    changeIconsByImportant (isImportant) {
      if (isImportant) {
        return ['fas', 'star']
      }
      return ['far', 'star']
    },
    changeIconsByCheck (isCheck) {
      if (isCheck) {
        return ['fas', 'check-square']
      }
      return ['far', 'square']
    }
  }
}
</script>

<style>

</style>



